import moment from 'moment';
import crypto from 'crypto';
import {BaseClass} from './baseClass.js';

export class Weixin extends BaseClass{
	constructor() {
		super();
		this.ready = false;
		this.appId = 'wx78bdfeded138667e';
		this.apiList = [
			'onMenuShareTimeline',
			'onMenuShareAppMessage',
			'onMenuShareQQ',
			'onMenuShareWeibo',
			'onMenuShareQZone'
		]
	}

	async init() {
		if(!this.isWeixin()){
			this.ready = false;
			return {notWeixin:true,msg:''};
		}
		if(window.wx){
			let options = {
			    debug: false, // ,apialertpclogpc
			    appId: this.appId, // 
			    timestamp: moment().unix(), // 
			    nonceStr: 'u77wxbytyanddcc', // 
			    signature: '',// 1
			    jsApiList: this.apiList // JSJS2
			}

			let result;
			try {
				result = await $.get('/weixin/ticket-api');
			}catch(e) {
				return {err:'get weixin ticket err',msg:'ticket,.(101)'};
			}

			if(result.err){
				return {err:result.err,msg:'ticket,.(102)'};
			}

			let jsapi_ticket = result.ticket;

			options.signature = this.getSignature(options,jsapi_ticket);

			return await new Promise((resolve,reject) => {
				wx.ready(() => {
					this.ready = true;
					resolve({msg:'ok'});
				});
				wx.error((err) => {
					this.ready = false;
					resolve({msg:'SDK,.',err:err});
				});
				wx.config(options);
			});
		}else{
			this.ready = false;
			return {err:'weixin sdk not load.',msg:'SDK,.'};
		}
	}

	getSignature(options,jsapi_ticket){
		let url = window.location.href.split('#')[0];
		let str = `jsapi_ticket=${jsapi_ticket}&noncestr=${options.nonceStr}&timestamp=${options.timestamp}&url=${url}`;
		return crypto.createHash('sha1').update(str, 'utf-8').digest('hex');
	}

	checkApi() {
		return new Promise((resolve,reject) => {
			if(!this.ready){
				resolve({err:'weixin sdk not ready.',msg:'API,SDK,.'});
				return false;
			}

			wx.checkJsApi({
				jsApiList:this.apiList,
				success:res => {
					if(res.errMsg == 'checkJsApi:ok' && res.checkResult.onMenuShareTimeline == true){
						resolve({msg:'ok'});
					}else {
						resolve({msg:'API,,.',err:'api check failed'});
					}
				}
			})
		});
	}

	isWeixin(){
		var ua = navigator.userAgent.toLowerCase();
		if(ua.match(/MicroMessenger/i)=="micromessenger") {
			return true;
	 	} else {
			return false;
		}
	}

	getAuth() {
		let url = `https://open.weixin.qq.com/connect/oauth2/authorize?appid=${this.appId}&redirect_uri=${encodeURIComponent(window.location.href)}&response_type=code&scope=snsapi_base&state=u77wx#wechat_redirect`;
		window.location.href = url;
	}
}


